@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";

  font-display: swap;
  src:
    url("../fonts/RobotoCondensed-300.woff2") format("woff2"),
    url("../fonts/RobotoCondensed-300.woff") format("woff");
}

